export default "import React from 'react';\nimport Field from 'terra-form-field';\nimport Input from 'terra-form-input';\n\nconst DefaultExample = () => (\n  <div>\n    <Field label=\"Default\">\n      <Input\n        name=\"default input\"\n        defaultValue=\"Default Input  Uncontrolled\"\n        ariaLabel=\"Default\"\n      />\n    </Field>\n  </div>\n);\n\nexport default DefaultExample;\n"